                <b-form-input id='input_shain_cd' v-model='wrapperEditShain.shainResponse.shainCd'></b-form-input>
              </b-form-group>
            </b-col>

            <b-col md='5'>
              <!-- 3. shain.shain_nm -->
              <b-form-group label='名前' label-for='input_shain_nm' class='label_input'>
                <b-form-input id="input_shain_nm" v-model='wrapperEditShain.shainResponse.shainNm'></b-form-input>
              </b-form-group>
            </b-col>
          </b-row>

          <b-row class='px-4 py-3'>
            <b-col md='5'>
              <!-- 4. mst_bumon.bumon_nm on shain.bumon_cd -->
              <b-form-group label='部門' label-for='input-horizontal' class='label_input'>
                <b-form-input></b-form-input>
              </b-form-group>
            </b-col>

            <b-col md='5'>
              <!-- 5. mst_yakushoku.name on shain.shain_cd -->
              <b-form-group label='役職' label-for='input-horizontal' class='label_input'>
                <b-form-input></b-form-input>
              </b-form-group>
            </b-col>
          </b-row>

          <b-row class='px-4 py-3'>
            <b-col md='5'>
              <!-- 6. shain.password -->
              <b-form-group label='パスワード' label-for='input-horizontal' class='label_input'>
                <b-form-input type='password'></b-form-input>
              </b-form-group>
            </b-col>
          </b-row>
        </div>

        <div class='user_infor_check'>
          <div class='px-4 py-3'>
            <div class='h4 mt-5'>アラート通知変更可否</div>

            <section class='mt-4 ml-5'>
              <b-row>
                <b-col cols='auto' class='h4'>
                  <div class='btn_show_hide' @click='()=>isTableUser=!isTableUser' v-b-toggle.table_user>
                    アラート処理
                    <i v-if='isTableUser' class='fas fa-angle-down'></i>
                    <i v-if='!isTableUser' class='fas fa-angle-up'></i>
                  </div>
                </b-col>
              </b-row>

              <b-collapse visible id='table_user'>
                <b-row class='account_receivables_condition'>
                  <b-col cols='auto' class='pl-0 account_receivables_condition_table'>
                    <b-table-simple responsive>
                      <b-thead>
                        <b-tr>
                          <b-th>発生トリガー</b-th>
                          <b-th>通知</b-th>
                        </b-tr>
                      </b-thead>

                      <b-tbody>
                        <b-tr class='border_bottom'>
                          <b-td>申請</b-td>
                          <b-td>
                            <!-- 11. shain.is_alerted_for_application -->
                            <b-form-checkbox button button-variant='light' class='position_check'>
                              <div class='border_check'>
                                <i class='fas fa-check'></i>
                              </div>
                            </b-form-checkbox>
                          </b-td>
                        </b-tr>

                        <b-tr class='border_bottom'>
                          <b-td>修正依頼</b-td>
                          <b-td>
                            <!-- 12. shain.is_alerted_for_modification_request -->
                            <b-form-checkbox button button-variant='light' class='position_check'>
                              <div class='border_check'>
                                <i class='fas fa-check'></i>
                              </div>
                            </b-form-checkbox>
                          </b-td>
                        </b-tr>

                        <b-tr class='border_bottom'>
                          <b-td>却下</b-td>
                          <b-td>
                            <!-- 13. shain.is_alerted_for_rejection -->
                            <b-form-checkbox button button-variant='light' class='position_check'>
                              <div class='border_check'>
                                <i class='fas fa-check'></i>
                              </div>
                            </b-form-checkbox>
                          </b-td>
                        </b-tr>

                        <b-tr class='border_bottom'>
                          <b-td>承認</b-td>
                          <b-td>
                            <!-- 14. shain.is_alerted_for_approval -->
                            <b-form-checkbox button button-variant='light' class='position_check'>
                              <div class='border_check'>
                                <i class='fas fa-check'></i>
                              </div>
                            </b-form-checkbox>
                          </b-td>
                        </b-tr>

                        <b-tr class='border_bottom'>
                          <b-td>差し戻し</b-td>
                          <b-td>
                            <!-- 15. shain.is_alerted_for_sending_request_back -->
                            <b-form-checkbox button button-variant='light' class='position_check'>
                              <div class='border_check'>
                                <i class='fas fa-check'></i>
                              </div>
                            </b-form-checkbox>
                          </b-td>
                        </b-tr>

                        <b-tr class='border_bottom'>
                          <b-td>差し戻し確認</b-td>
                          <b-td>
                            <!-- 16. shain.is_alerted_for_confirming_send_request_back -->
                            <b-form-checkbox button button-variant='light' class='position_check'>
                              <div class='border_check'>
                                <i class='fas fa-check'></i>
                              </div>
                            </b-form-checkbox>
                          </b-td>
                        </b-tr>

                        <b-tr class='border_bottom'>
                          <b-td>支払日入力</b-td>
                          <b-td>
                            <!-- 17. shain.is_alerted_for_input_payment_date -->
                            <b-form-checkbox button button-variant='light' class='position_check'>
                              <div class='border_check'>
                                <i class='fas fa-check'></i>
                              </div>
                            </b-form-checkbox>
                          </b-td>
                        </b-tr>

                        <b-tr class='border_bottom'>
                          <b-td>決裁確認</b-td>
                          <b-td>
                            <!-- 18. shain.is_alerted_for_confirming_settlement -->
                            <b-form-checkbox button button-variant='light' class='position_check'>
                              <div class='border_check'>
                                <i class='fas fa-check'></i>
                              </div>
                            </b-form-checkbox>
                          </b-td>
                        </b-tr>

                        <b-tr class='border_bottom'>
                          <b-td>申請代理人設定</b-td>
                          <b-td>
                            <!-- 19. shain.is_alerted_for_application_deputy -->
                            <b-form-checkbox button button-variant='light' class='position_check'>
                              <div class='border_check'>
                                <i class='fas fa-check'></i>
                              </div>
                            </b-form-checkbox>
                          </b-td>
                        </b-tr>

                        <b-tr class='border_bottom'>
                          <b-td>承認代理人設定</b-td>
                          <b-td>
                            <!-- 20. shain.is_alerted_for_approval_deputy -->
                            <b-form-checkbox button button-variant='light' class='position_check'>
                              <div class='border_check'>
                                <i class='fas fa-check'></i>
                              </div>
                            </b-form-checkbox>
                          </b-td>
                        </b-tr>

                        <b-tr class='border_bottom'>
                          <b-td>担当者変更</b-td>
                          <b-td>
                            <!-- 20. shain.is_alerted_for_changing_charge -->
                            <b-form-checkbox button button-variant='light' class='position_check'>
                              <div class='border_check'>
                                <i class='fas fa-check'></i>
                              </div>
                            </b-form-checkbox>
                          </b-td>
                        </b-tr>
                      </b-tbody>
                    </b-table-simple>
                  </b-col>
                </b-row>
              </b-collapse>
            </section>

            <section class='mt-4 ml-5'>
              <b-row>
                <b-col cols='auto' class='h4'>
                  <div class='btn_show_hide' @click='()=>isTableUserRemind=!isTableUserRemind' v-b-toggle.table_user_remind>
                    リマインドアラート処理
                    <i v-if='isTableUserRemind' class='fas fa-angle-down'></i>
                    <i v-if='!isTableUserRemind' class='fas fa-angle-up'></i>
                  </div>
                </b-col>
              </b-row>

              <b-collapse visible id='table_user_remind'>
                <b-row class='account_receivables_condition'>
                  <b-col cols='auto' class='pl-0 account_receivables_condition_table'>
                    <b-table-simple responsive>
                      <b-thead>
                        <b-tr>
                          <b-th>発生トリガー</b-th>
                          <b-th>通知</b-th>
                        </b-tr>
                      </b-thead>

                      <b-tbody>
                        <b-tr class='border_bottom'>
                          <b-td>リマインドアラート  作成中</b-td>
                          <b-td>
                            <!-- 23. shain.is_alerted_for_being_created -->
                            <b-form-checkbox button button-variant='light' class='position_check'>
                              <div class='border_check'>
                                <i class='fas fa-check'></i>
                              </div>
                            </b-form-checkbox>
                          </b-td>
                        </b-tr>

                        <b-tr class='border_bottom'>
                          <b-td>リマインドアラート  確認待ち</b-td>
                          <b-td>
                            <!-- 24. shain.is_alerted_for_waiting_confirmation -->
                            <b-form-checkbox button button-variant='light' class='position_check'>
                              <div class='border_check'>
                                <i class='fas fa-check'></i>
                              </div>
                            </b-form-checkbox>
                          </b-td>
                        </b-tr>

                        <b-tr class='border_bottom'>
                          <b-td>リマインドアラート  申請待ち</b-td>
                          <b-td>
                            <!-- 25. shain.is_alerted_for_waiting_application -->
                            <b-form-checkbox button button-variant='light' class='position_check'>
                              <div class='border_check'>
                                <i class='fas fa-check'></i>
                              </div>
                            </b-form-checkbox>
                          </b-td>
                        </b-tr>

                        <b-tr class='border_bottom'>
                          <b-td>リマインドアラート  承認待ち</b-td>
                          <b-td>
                            <!-- 26. shain.is_alerted_for_waiting_approval -->
                            <b-form-checkbox button button-variant='light' class='position_check'>
                              <div class='border_check'>
                                <i class='fas fa-check'></i>
                              </div>
                            </b-form-checkbox>
                          </b-td>
                        </b-tr>

                        <b-tr class='border_bottom'>
                          <b-td>リマインドアラート  申請待ち(差し戻し中)</b-td>
                          <b-td>
                            <!-- 27. shain.is_alerted_for_waiting_application_on_sending_back -->
                            <b-form-checkbox button button-variant='light' class='position_check'>
                              <div class='border_check'>
                                <i class='fas fa-check'></i>
                              </div>
                            </b-form-checkbox>
                          </b-td>
                        </b-tr>

                        <b-tr class='border_bottom'>
                          <b-td>リマインドアラート  承認待ち(差し戻し中)</b-td>
                          <b-td>
                            <!-- 28. shain.is_alerted_for_waiting_approval_on_sending_back -->
                            <b-form-checkbox button button-variant='light' class='position_check'>
                              <div class='border_check'>
                                <i class='fas fa-check'></i>
                              </div>
                            </b-form-checkbox>
                          </b-td>
                        </b-tr>

                        <b-tr class='border_bottom'>
                          <b-td>リマインドアラート  決裁確認待ち</b-td>
                          <b-td>
                            <!-- 29. shain.is_alerted_for_waiting_confirming_settlement -->
                            <b-form-checkbox button button-variant='light' class='position_check'>
                              <div class='border_check'>
                                <i class='fas fa-check'></i>
                              </div>
                            </b-form-checkbox>
                          </b-td>
                        </b-tr>

                        <b-tr class='border_bottom'>
                          <b-td>マスタ変更適用処理</b-td>
                          <b-td>
                            <!-- 30. shain.is_alerted_for_updating_database -->
                            <b-form-checkbox button button-variant='light' class='position_check'>
                              <div class='border_check'>
                                <i class='fas fa-check'></i>
                              </div>
                            </b-form-checkbox>
                          </b-td>
                        </b-tr>
                      </b-tbody>
                    </b-table-simple>
                  </b-col>
                </b-row>
              </b-collapse>
            </section>
          </div>
        </div>
      </div>

      <div class='btnForRequest'>
        <b-button variant='none' class='btn-blue'>保存</b-button>
      </div>
    </b-container>
   </section>
</template>

<script>
import ShainService from '@/services/ShainService';
import WrapperEditShain from '@/models/WrapperEditShain';

export default {
  name: 'Edit',
  data() {
    return {
      isTableUser: true,
      isTableUserRemind: true,
      wrapperEditShain: new WrapperEditShain()
    }
  },
  methods: {
    getInformation() {
      ShainService.getInformation()
      .then(res => {
        Object.assign(this.wrapperEditShain.shainResponse, res.data);
      })
      .catch(err => {
        console.log(err);
      });
    }
  },
  created() {
    this.getInformation();
  }
}
</script>

<style>
.account_receivables_condition {
  margin-left: 5rem;
  margin-top: 1rem;
}

.account_receivables_condition_table table {
  width: auto;
}

.account_receivables_condition_table tr th,
.account_receivables_condition_table tr td {
  font-size: 0.8rem;
  border-top: 0;
}

.account_receivables_condition_table tr th:first-child,
.account_receivables_condition_table tr td:first-child  {
  text-align: left;
  padding-left: 1.5rem;
}

.account_receivables_condition_table tr td:first-child {
  padding-left: 2.5rem;
}

.account_receivables_condition_table tr th:last-child,
.account_receivables_condition_table tr td:last-child {
  text-align: right;
  padding-right: 1.5rem;
}

.account_receivables_condition_table .border_check {
  width: 2rem;
  height: 2rem;
  padding: 4px;
  border: 1px solid grey;
  border-radius: 2px;
}

.account_receivables_condition_table .position_check {
  margin-right: -5px;
  margin-left: 10rem;
}

.account_receivables_condition_table .btn_show_hide {
  cursor: pointer;
}

.account_receivables_condition_table .border_bottom {
  border-bottom: 1px solid grey;
}

</style>